html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  margin: 0 auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #2c2e3f;
  background-color: #e5e5e5; }

ul {
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  color: #141517; }

.main {
  width: 1920px;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .main {
      width: 768px; } }
  @media (min-width: 320px) {
    .main {
      width: 320px; } }
  .main .container {
    padding: 0;
    background-color: transparent; }

