.button {
  font-family: "Monserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  border-radius: 10px; }
  .button:hover {
    background-color: yellow; }
  .button:active {
    background-color: yellow;
    color: rgba(29, 46, 91, 0.302); }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

.page-wrapper {
  width: 1920px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .page-wrapper {
      width: 1023px; } }
  @media (max-width: 767px) {
    .page-wrapper {
      width: 767px; } }

.page-header {
  width: 1920px;
  padding-top: 39px;
  padding-bottom: 38px;
  padding-left: 375px;
  padding-right: 375px;
  background-color: #ffffff; }
  @media (max-width: 1023px) {
    .page-header {
      width: 1023px;
      padding-top: 0;
      padding-bottom: 0;
      padding-left: 21px;
      padding-right: 0; } }
  @media (max-width: 767px) {
    .page-header {
      width: 767px;
      padding-left: 16px; } }

.site-nav {
  display: flex;
  flex-direction: row; }

.site-nav__close {
  display: none; }

.site-nav__mobile-menu {
  display: none; }
  @media (max-width: 1023px) {
    .site-nav__mobile-menu {
      display: block;
      background-color: #fe7865;
      margin-left: auto; } }

.site-nav__logo {
  margin-right: 623px; }

.site-nav__list {
  margin: 0;
  padding-left: 0;
  display: flex;
  align-items: center; }
  @media (max-width: 1023px) {
    .site-nav__list {
      display: none; } }

.site-nav__list-item {
  display: inline-block; }
  .site-nav__list-item--phone {
    margin-left: 53px;
    display: flex;
    align-items: center; }

.site-nav__list-link {
  color: #fe7865;
  font-size: 14px;
  font-weight: 500;
  line-height: 27px; }
  .site-nav__list-link--active {
    color: #141517;
    margin-right: 40px; }
  .site-nav__list-link--phone {
    font-size: 18px;
    font-weight: 400; }

.burger-menu {
  background-color: #fe7865;
  outline: none;
  border: none;
  width: 72px;
  height: 50px; }

.intro {
  height: 860px;
  background: #add8e6 url("../img/header-greece-desktop.jpg") no-repeat top center; }

.intro__title {
  margin: 0; }

.page-footer {
  width: 1920px;
  padding-left: 375px;
  padding-right: 375px;
  height: 270px;
  padding-bottom: 75px;
  background-color: #ffffff; }
  @media (max-width: 1023px) {
    .page-footer {
      height: 492.4px;
      width: 768px;
      padding-top: 80.4px;
      padding-left: 21px;
      padding-right: 21px;
      padding-bottom: 50px; } }

.footer-menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
  @media (max-width: 1023px) {
    .footer-menu {
      flex-wrap: wrap; } }

.footer-menu__item {
  min-width: 390px;
  border-right: 1px solid #d7d9df; }
  @media (max-width: 1023px) {
    .footer-menu__item--next-travels {
      min-width: 395px; } }
  .footer-menu__item--contacts {
    display: flex;
    flex-direction: column;
    padding-left: 90px; }
    @media (max-width: 1023px) {
      .footer-menu__item--contacts {
        min-width: 331px;
        border-right: none;
        padding-left: 50px; } }
  .footer-menu__item--corporative {
    padding-left: 49px;
    border-right: none; }
    @media (max-width: 1023px) {
      .footer-menu__item--corporative {
        width: 100%;
        padding-left: 0;
        text-align: center;
        border-top: 1px solid #d7d9df;
        padding-top: 30px; } }

.footer-menu__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;
  margin: 0;
  margin-bottom: 20px; }

.footer-menu__country-list {
  padding-left: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 195px; }
  @media (max-width: 1023px) {
    .footer-menu__country-list {
      height: 180px; } }

.footer-menu__country-item {
  margin-bottom: 10px; }

.footer-menu__country-link,
.footer-menu__paragraph {
  font-size: 14px;
  line-height: auto;
  font-weight: 400;
  opacity: 50%; }

.footer-menu__link {
  color: #fe7865;
  padding-left: 30px; }
  .footer-menu__link--mail {
    font-size: 14px;
    line-height: auto;
    font-weight: 400;
    position: relative; }
    .footer-menu__link--mail::before {
      content: url("../img/mail.svg");
      position: absolute;
      top: 0;
      display: block;
      left: 0; }
  .footer-menu__link--phone {
    font-size: 16px;
    line-height: 32px;
    position: relative; }
    .footer-menu__link--phone::before {
      content: url("../img/footer-phone.svg");
      position: absolute;
      top: 0;
      display: block;
      left: 0; }
